import {ILogger} from './ILogger'
import {Constants} from './Constants'
import { Util } from './Jupyter';
export class Utility{
    
    public static isNullOrEmptyString(value: string): boolean {
        if (typeof(value) === 'undefined') {
            return true;
        }

        if (value === null) {
            return true;
        }

        if (value.length === 0){
            return true;
        }

        return false;
    }

    public static isNullOrUndefined(value: any): boolean {
        if (typeof(value) === 'undefined') {
            return true;
        }

        if (value === null) {
            return true;
        }

        return false;
    }

    public static log(message: string): void {
        let logger: ILogger = (window as any)._logger;
        if (logger) {
            logger.log(message);
        }
        else {
            var divlog = document.getElementById("DivLog");
            if (divlog) {
                var div = document.createElement("div");
                div.appendChild(document.createTextNode(message));
                document.getElementById("DivLog").appendChild(div);
            }
            else {
                console.log(message);
            }
        }
    }

    public static logConsole(message: string): void {
        let logger: ILogger = (window as any)._logger;
        if (logger) {
            logger.logConsole(message);
        }
        else {
            console.log(message);
        }
    }

    public static logError(error: string | Error): void {
        let message: string;
        if (error instanceof Error) {
            message = error.name + ':' + error.message;
        }
        else if (typeof(error) === 'string') {
            message = error;
        }
        else {
            message = JSON.stringify(error);
        }
        
        let logger: ILogger = (window as any)._logger;
        if (logger) {
            logger.logError(message);
        }
        else {
            console.error(message);
        }
    }

    public static getJupyterConnectionFromLocalStorageOrDefault() {
        let jupyterUrl = localStorage.getItem(Constants.localStorageKeyConnectionUrl);
        let jupyterToken = localStorage.getItem(Constants.localStorageKeyConnectionToken);
        let jupyterNotebookName = localStorage.getItem(Constants.localStorageKeyConnectionNotebookName);
        if (Utility.isNullOrEmptyString(jupyterUrl)) {
            jupyterUrl = 'http://localhost:8888';
            jupyterToken = 'exceljupyter';
            jupyterNotebookName = 'HelloWorld.ipynb';
        }

        return {
            url: jupyterUrl,
            token: jupyterToken,
            notebookName: jupyterNotebookName
        }
    }

    public static appendQueryString(url: string, queryStrings: {[key: string]: string}): string{
        let query = '';
        for (let key in queryStrings) {
            if (query.length > 0) {
                query = query + '&';
            }
            query = query + encodeURIComponent(key) + '=' + encodeURIComponent(queryStrings[key]);
        }

        if (query.length === 0) {
            return url;
        }

        const index = url.indexOf('?');
        if (index < 0){
            url = url + '?';    
        }
        else {
            url = url + '&';
        }

        url = url + query;

        return url;
    }
}