/// <reference path="./references/customfunctions.d.ts" />
/// <reference path="./references/office.d.ts" />
import * as React from 'react'
import * as ReactDOM from 'react-dom'
import { initializeIcons } from 'office-ui-fabric-react/lib/Icons';

import { initCustomFunctionInvoker } from './WebServiceCustomFunctionInvoker';
import {CustomFunctionPane} from './components/CustomFunctionPane';
import {WebServiceCustomFunctionUtility} from './WebServiceCustomFunctionUtility';
import {Utility} from './Utility';

if (CustomFunctions) {
    CustomFunctions.delayInitialization();
}

initializeIcons(/* optional base url */);

Office.onReady(() => {
    console.log("Office.onReady");
    initCustomFunctionInvoker();
});

window.addEventListener('load', () => {
    if (document.getElementById('DivApp')) {
        ReactDOM.render(
            <CustomFunctionPane
            />
            , document.getElementById("DivApp")
        );
    }
});
