/// <reference path="references/office.d.ts" />
import { Utility } from './Utility';

export class WebServiceCustomFunctionUtility {
    static fetchFunctionsMetadata(): Promise<string> {
        return fetch('/functions')
        .then((response)=> {
            if (response.status !== 200) {
                throw new Error(`Invalid response, status = ${response.status}`);
            }

            const contentType = response.headers.get('content-type');
            if (!contentType || contentType.indexOf('application/json') < 0) {
                throw new Error(`Invalid response, contentType= ${contentType}`);
            }

            return response.text();
        })
        .then((metadata)=> {
            const metadataObj = JSON.parse(metadata);
            const normalizedMetadata = JSON.stringify(metadataObj);
            return normalizedMetadata;
        })
        .catch((ex) => {
            Utility.logError(ex);
            throw ex;
        });
    }

    static registerFunctions(metadata: string): Promise<void> {
        if (typeof(Excel) !== 'undefined' && Excel.CustomFunctionManager) {
            return Excel.CustomFunctionManager.register(metadata, "");
        }
        else {
            return Promise.resolve();
        }
    }
}
