/// <reference path="../references/office.d.ts" />

import * as React from 'react'
import * as ReactDOM from 'react-dom'
import {TextField} from 'office-ui-fabric-react/lib/TextField'
import { Link } from 'office-ui-fabric-react/lib/Link';
import {Pivot, PivotItem} from 'office-ui-fabric-react/lib/Pivot';
import {v4 as uuidv4} from 'uuid';
import { Utility } from '../Utility';
import {WebServiceCustomFunctionUtility} from '../WebServiceCustomFunctionUtility'
import { Button } from 'office-ui-fabric-react/lib/Button';
import {CustomFunctionTestPane} from './CustomFunctionTestPane'


export interface ICustomFunctionPaneProps {

}

export interface ICustomFunctionPaneState {
	manifest?: string;
	functions?: any[];
	image?: string;
}

export class CustomFunctionPane extends React.Component<ICustomFunctionPaneProps, ICustomFunctionPaneState> {
    constructor(props: ICustomFunctionPaneProps){
        super(props);
        this.state = {
			functions: []
		};
	}
	
	public componentDidMount() {
		var savedMetadata: string = null;
		let officeOnReady = false;
		WebServiceCustomFunctionUtility.fetchFunctionsMetadata()
		.then((metadata) => {
			savedMetadata = metadata;
			let metadataObj = JSON.parse(metadata);
			let functions = metadataObj.functions || [];
			this.setState({functions: functions});
			Office.onReady(() => {
					Utility.log(`Register ${metadata}`);
					officeOnReady = true;
					return WebServiceCustomFunctionUtility.registerFunctions(metadata)
				});
			});
		
		window.setInterval(() => {
			WebServiceCustomFunctionUtility.fetchFunctionsMetadata()
			.then((metadata) => {
				if (savedMetadata != metadata) {
					savedMetadata = metadata;
					let metadataObj = JSON.parse(metadata);
					let functions = metadataObj.functions || [];
					this.setState({functions: functions});
					if (officeOnReady) {
						Utility.log(`Register ${metadata}`);
						return WebServiceCustomFunctionUtility.registerFunctions(metadata)
					}
				}
			});
		},
		30000);
	}

    public render() {
        return (
            <div>
				<Pivot>
					<PivotItem
						headerText="Functions"
						>
						{
							this.state.functions.map((func) => {
								return (
									<div key={func.id}>{func.name}(
										{
											func.parameters && 
											func.parameters.map((par, index) => {
												return (index > 0 ? "," : "") + " " + par.name;
											})
										}
									)</div>
								);
							})
						}
					</PivotItem>
					<PivotItem
						headerText="Developer"
						>
						<div>
							<button onClick={() => this.generateManifestButtonClick()}>Generate Manifest</button>
							<button onClick={() => this.registerButtonClick()}>Register Functions With Excel</button>
							<div>Functions</div>
							{
								this.state.functions.map((func) => {
									return (
										<CustomFunctionTestPane key={func.id} func={func} onReceiveImage = {(image) => this.onReceiveImage(image)} />
									);
								})
							}
						</div>
						<div>
							{
								this.state.image && <img src={`data:image/png;base64,${this.state.image}`}/>
							}
						</div>
						<div>
							Test Sections:
						</div>
						<div>
							<button onClick={() => this.refreshButtonClick()}>Refresh</button>
							<button onClick={() => this.testPostButtonClick()}>Test Post</button>
							<button onClick={() => this.testGetButtonClick()}>Test Get</button>
							<div>
								{this.state.manifest}
							</div>
						</div>
					</PivotItem>
				</Pivot>
            </div>
        )
	}
	
	private onReceiveImage(image: string): void {
		this.setState({image: image});
	}

    private refreshButtonClick(): void {
        window.location.reload();
    }

    private testGetButtonClick(): void {
        let url = "/functions";
        fetch(url)
        .then((response) => {
            return response.text()
        })
        .then((responseText) => {
            Utility.log(responseText);
        })
        .catch((ex) => {
            Utility.logError(ex);
        });
    }

    private testPostButtonClick(): void {
        let url = "/functions";
        let payload = {
            id: "myadd",
            parameters: [3,4]
        };

        fetch(url, {
            method: 'POST',
            body: JSON.stringify(payload),
            headers: {
                "Content-Type": "application/json"
            }
        })
        .then((response) => {
            return response.text()
        })
        .then((responseText) => {
            Utility.log(responseText);
        })
        .catch((ex) => {
            Utility.logError(ex);
        });
    }

    private registerButtonClick(): void {
		WebServiceCustomFunctionUtility.fetchFunctionsMetadata()
		.then((metadata) => {
			return WebServiceCustomFunctionUtility.registerFunctions(metadata);
		})
		.catch((ex) => {
			Utility.logError(ex);
		});
    }

    private generateManifestButtonClick(): void {
        var hostUrl: string = window.location.protocol + "//" + window.location.host;
        var addinId: string = uuidv4();
        if (window.localStorage) {
            const key = 'ExcelJupyter.AddinId';
            addinId = window.localStorage.getItem(key);
            if (!addinId) {
                addinId = uuidv4();
                window.localStorage.setItem(key, addinId);
            }
        }

        var manifestFormat = 
`<?xml version="1.0" encoding="utf-8"?>
<OfficeApp xmlns="http://schemas.microsoft.com/office/appforoffice/1.1"
 xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance"
 xmlns:bt="http://schemas.microsoft.com/office/officeappbasictypes/1.0"
 xmlns:ov="http://schemas.microsoft.com/office/taskpaneappversionoverrides"
 xsi:type="TaskPaneApp">
	<Id>${addinId}</Id>
	<Version>1.0.0.0</Version>
	<ProviderName>Microsoft Office Platform Team</ProviderName>
	<DefaultLocale>en-US</DefaultLocale>
	<DisplayName DefaultValue="Python" />
	<Description DefaultValue="Python" />
	<IconUrl DefaultValue="https://shaofengzhu.github.io/office-addin/excel-codespace/agave/images/button32x32.png" />
	<SupportUrl DefaultValue="https://www.microsoft.com" />
	<Hosts>
		<Host Name="Workbook" />
	</Hosts>
	<DefaultSettings>
		<SourceLocation DefaultValue="${hostUrl}/functions.html"/>
	</DefaultSettings>
	<Permissions>ReadWriteDocument</Permissions>
	<VersionOverrides xmlns="http://schemas.microsoft.com/office/taskpaneappversionoverrides" xsi:type="VersionOverridesV1_0">
		<Hosts>
			<Host xsi:type="Workbook">
        		<Runtimes>
          			<Runtime resid="OEP.SharedRuntime.Url" lifetime="long" />
        		</Runtimes>
				<AllFormFactors>
					<ExtensionPoint xsi:type="CustomFunctions">
						<Script>
							<SourceLocation resid="OEP.UDF.Js" />
						</Script>
						<Page>
							<SourceLocation resid="OEP.SharedRuntime.Url"/>
						</Page>
						<Metadata>
							<SourceLocation resid="OEP.UDF.Json" />
						</Metadata>
						<Namespace resid="OEP.UDF.Namespace" />
					</ExtensionPoint>
				</AllFormFactors>
				<DesktopFormFactor>
				  	<!--"This code enables a customizable message to be displayed when the add-in is loaded successfully upon individual install."-->
				  	<GetStarted>
						<!-- Title of the Getting Started callout. resid points to a ShortString resource -->
						<Title resid="OEP.GetStarted.Title"/>

						<!-- Description of the Getting Started callout. resid points to a LongString resource -->
						<Description resid="OEP.GetStarted.Description"/>

						<!-- Point to a url resource which details how the add-in should be used. -->
						<LearnMoreUrl resid="OEP.GetStarted.LearnMoreUrl"/>
				  	</GetStarted>

		  			<!-- Function file is a HTML page that includes the JavaScript where functions for ExecuteAction will be called. 
						Think of the FunctionFile as the code behind ExecuteFunction. -->
		  			<FunctionFile resid="OEP.SharedRuntime.Url" />

				  	<!-- PrimaryCommandSurface is the main Office Ribbon. -->
				  	<ExtensionPoint xsi:type="PrimaryCommandSurface">
						<!-- Use OfficeTab to extend an existing Tab. Use CustomTab to create a new tab. -->
						<CustomTab id="OEP.TabJupyter">
							<!-- Ensure you provide a unique id for the group. Recommendation for any IDs is to namespace using your company name. -->
							<Group id="OEP.Group1">
								<!-- Label for your group. resid must point to a ShortString resource. -->
								<Label resid="OEP.Group1Label" />
								<!-- Icons. Required sizes 16,32,80, optional 20, 24, 40, 48, 64. Strongly recommended to provide all sizes for great UX. -->
								<!-- Use PNG icons. All URLs on the resources section must use HTTPS. -->
								<Icon>
									<bt:Image size="16" resid="OEP.tpicon_16x16" />
									<bt:Image size="32" resid="OEP.tpicon_32x32" />
									<bt:Image size="80" resid="OEP.tpicon_80x80" />
								</Icon>
								<!-- Control. It can be of type "Button" or "Menu". -->
								<Control xsi:type="Button" id="OEP.TaskpaneButton">
									<Label resid="OEP.TaskpaneButton.Label" />
									<Supertip>
										<!-- ToolTip title. resid must point to a ShortString resource. -->
										<Title resid="OEP.TaskpaneButton.Label" />
										<!-- ToolTip description. resid must point to a LongString resource. -->
										<Description resid="OEP.TaskpaneButton.Tooltip" />
									</Supertip>
									<Icon>
										<bt:Image size="16" resid="OEP.tpicon_16x16" />
										<bt:Image size="32" resid="OEP.tpicon_32x32" />
										<bt:Image size="80" resid="OEP.tpicon_80x80" />
									</Icon>

									<!-- This is what happens when the command is triggered (E.g. click on the Ribbon). Supported actions are ExecuteFunction or ShowTaskpane. -->
									<Action xsi:type="ShowTaskpane">
										<TaskpaneId>CodeEditor</TaskpaneId>
										<!-- Provide a url resource id for the location that will be displayed on the task pane. -->
										<SourceLocation resid="OEP.SharedRuntime.Url" />
									</Action>
								</Control>
							</Group>
							<Label resid="OEP.JupyterTab.Label" />
						</CustomTab>
				  </ExtensionPoint>
				</DesktopFormFactor>
			</Host>
		</Hosts>
		<Resources>
			<bt:Images>
				<bt:Image id="OEP.tpicon_16x16" DefaultValue="https://shaofengzhu.github.io/office-addin/excel-codespace/agave/images/button16x16.png" />
				<bt:Image id="OEP.tpicon_32x32" DefaultValue="https://shaofengzhu.github.io/office-addin/excel-codespace/agave/images/button32x32.png" />
				<bt:Image id="OEP.tpicon_80x80" DefaultValue="https://shaofengzhu.github.io/office-addin/excel-codespace/agave/images/button80x80.png" />
			</bt:Images>
			<bt:Urls>
				<bt:Url id="OEP.UDF.Json" DefaultValue="https://shaofengzhu.github.io/office-addin/excel-codespace/agave/udf.json" >
				</bt:Url>
				<bt:Url id="OEP.UDF.Js" DefaultValue="https://shaofengzhu.github.io/office-addin/excel-codespace/agave/udf.js" >
				</bt:Url>
				<bt:Url id="OEP.SharedRuntime.Url" DefaultValue="${hostUrl}/functions.html" />
				<bt:Url id="OEP.GetStarted.LearnMoreUrl" DefaultValue="https://go.microsoft.com/fwlink/?LinkId=276812" />
			</bt:Urls>
			<bt:ShortStrings>
				<bt:String id="OEP.UDF.Namespace" DefaultValue="PYTHON" />
				<bt:String id="OEP.TaskpaneButton.Label" DefaultValue="Show Taskpane" />
				<bt:String id="OEP.Group1Label" DefaultValue="Python" />
				<bt:String id="OEP.GetStarted.Title" DefaultValue="Python" />
	  			<bt:String id="OEP.JupyterTab.Label" DefaultValue="Python" />
				<bt:String id="OEP.RegisterButton.Label" DefaultValue="Register" />
			</bt:ShortStrings>
			<bt:LongStrings>
				<bt:String id="OEP.TaskpaneButton.Tooltip" DefaultValue="Click to show Python" />
				<bt:String id="OEP.ConnectButton.Tooltip" DefaultValue="Connect to a Python" />
				<bt:String id="OEP.RegisterButton.Tooltip" DefaultValue="Register Custom Functions" />
				<bt:String id="OEP.GetStarted.Description" DefaultValue="Your add-in loaded succesfully. Go to the Python tab and click the 'Show Python' button to get started." />
			</bt:LongStrings>
		</Resources>
	</VersionOverrides>
</OfficeApp>
`;
        this.setState({
            manifest: manifestFormat
        });

        this.downloadToFile(manifestFormat, addinId + ".xml");
    }

    private downloadToFile(content, filename) {
        const a = document.createElement('a');
        const file = new Blob([content], {type: "text/xml"});
        
        a.href= URL.createObjectURL(file);
        a.download = filename;
        a.click();
      
        URL.revokeObjectURL(a.href);
    }
}
