/// <reference path="../references/customfunctions.d.ts" />

import * as React from 'react'
import * as ReactDOM from 'react-dom'
import {TextField} from 'office-ui-fabric-react/lib/TextField'
import { setUncaughtExceptionCaptureCallback } from 'process'
import { ThemeSettingName } from 'office-ui-fabric-react/lib/Styling';
import {WebServiceCustomFunctionUtility} from '../WebServiceCustomFunctionUtility';
import { Utility } from '../Utility';
import {InvokeResult} from '../WebServiceCustomFunctionInvoker';

export interface ICustomFunctionTestPaneProps
{
    func: CustomFunctionsMetadata.Function;
    onReceiveImage: (image: string) => void;
}

export interface ICustomFunctionTestPaneState
{
    parameters: string;
}

export class CustomFunctionTestPane extends React.Component<ICustomFunctionTestPaneProps, ICustomFunctionTestPaneState> {
    constructor(props) {
        super(props);
        this.state = {
            parameters: ""
        }
    }

    render() {
        return (
            <div>
                <div>
                    <span>
                        {this.props.func.name} (
                        {
                            this.props.func.parameters && this.props.func.parameters.map((par, index) => {
                                                        return (index > 0 ? "," : "") + " " + par.name;
                                                        })
                        }
                        )
                    </span>
                </div>
                <div>
                    <TextField 
                        prefix={"=PYTHON." + this.props.func.name + "("} 
                        suffix=")" 
                        onChange = {(evt, value) => this.onParametersTextChanged(value)} 
                        value={this.state.parameters} 
                        onKeyUp = {(evt) => this.onKeyUp(evt)} />
                </div>
            </div>
        );
    }

    private onParametersTextChanged(value): void {
        this.setState({parameters: value});
    }

    private onKeyUp(evt: React.KeyboardEvent<HTMLInputElement | HTMLTextAreaElement>): void {
        if (evt.keyCode == 13) {
            this.onInvokeButtonClick();
        }
    }

    private onInvokeButtonClick(): void {
        let payload = {
            id: this.props.func.id,
            parameters: JSON.parse("[" + this.state.parameters + "]")
        };

        let url = "/functions?invoke=" + encodeURIComponent(JSON.stringify(payload));

        // fetch(url, {
        //     method: 'POST',
        //     body: JSON.stringify(payload)
        // })

        fetch(url)
        .then((response) => {
            return response.text()
        })
        .then((text) => {
            Utility.log(text);
            const invokeResult: InvokeResult = JSON.parse(text);
            if (typeof(invokeResult.result) === 'object' && invokeResult.result.mimeType === "image/png") {
                if (this.props.onReceiveImage) {
                    this.props.onReceiveImage(invokeResult.result.data);
                }
            }
        });
    }
}