/// <reference path="./references/customfunctions.d.ts" />
import 'whatwg-fetch'
import {Utility} from './Utility';
import { urlencoded } from 'express';

export interface InvokeResult {
    error?: number;
    result?: any | {
        data: string,
        mimeType: string
    };
}

class WebServiceCustomFunctionInvoker implements CustomFunctions.CustomFunctionInvoker {

    invoke(functionName: string, parameters: any[], context: CustomFunctions.InvocationContext): void {
        let payload = {
            id: functionName,
            parameters: parameters
        };

        let url = "/functions?invoke=" + encodeURIComponent(JSON.stringify(payload));

        // fetch(url, {
        //     method: 'POST',
        //     body: JSON.stringify(payload)
        // })

        fetch(url)
        .then((response) => {
            return response.json()
        })
        .then((invokeResult: InvokeResult) => {
            if (invokeResult.error) {
                context.setError(CustomFunctions.ErrorCode.unknown);
            }
            else {
                if (!Array.isArray(invokeResult.result) && typeof(invokeResult.result) === "object") {
                    if (invokeResult.result.mimeType === "image/png") {
                        context.setResult("#IMAGE");
                        let address = context.address;
                        setTimeout(() => {
                            this.setImage(address, invokeResult.result.data);
                        }, 0);
                    }
                    else {
                        context.setError(CustomFunctions.ErrorCode.unknown);
                    }
                }
                else {
                    context.setResult(invokeResult.result);
                }
            }
        })
        .catch((ex) => {
            context.setError(CustomFunctions.ErrorCode.notAvailable, JSON.stringify(ex));
        });
    }

    private setImage(fullAddress: string, base64: string): void {
        Excel.run(async (context) => {
            var worksheet;
            var range;

            if (fullAddress) {
                const index = fullAddress.indexOf('!');
                var sheetName = fullAddress.substring(0, index);
                var address = fullAddress.substring(index + 1);

                worksheet = context.workbook.worksheets.getItem(sheetName);
                range = worksheet.getRange(address);
            }
            else {
                worksheet = context.workbook.worksheets.getActiveWorksheet();
                range = worksheet.getRange("A1");
            }

            var shapes = worksheet.shapes.load();
            await context.sync();
            for (let i = 0; i < shapes.items.length; i++) {
                shapes.items[i].delete();
            }

            range.load("top, left");
            await context.sync();

            let shape = shapes.addImage(base64);
            shape.left = range.left + 10;
            shape.top = range.top + 10;
            await context.sync();
        });        
    }
}

export function initCustomFunctionInvoker() {
    if (typeof(CustomFunctions) === 'object' && CustomFunctions.setCustomFunctionInvoker) {
        Utility.log("setCustomFunctionInvoker");        
        CustomFunctions.setCustomFunctionInvoker(new WebServiceCustomFunctionInvoker());
    }
}
